$(function(){
  var $sc = $('#screen-control');
  var $screens = $('#screens').find('.s-screens');
  
  
  $sc.append(function(){
    
    var result = '';
    
    $.each( $screens, function(i,e){
      
      var id = $(e).attr('id');
      
      result += (id) ? `<div class="s-control__item" data-screen="#${id}"></div>` : '';
      
    });
    
    return result;  
      
  });
  
  $('.s-control__item').click(function(){
    var $this = $(this);
    $('html, body').animate({
        scrollTop: $( $this.data('screen') ).offset().top
    }, 500);
  });
  
  
});
$(function(){
  
  var $screen_1 = $('#screen_1');
  var $screen_2 = $('#screen_2');
  var $screen_3 = $('#screen_3');
  var $screen_4 = $('#screen_4');
  
  var active = '';
  
  position();
  
  $(window).scroll(function(){
    position();
  });
  
  function position(){
    var e = $screen_1[0].getBoundingClientRect();
    var height = ( $('body').is('.mobile') ) ? -100 : $(window).outerHeight();
    
    active = '';
    
    if ( $screen_1[0].getBoundingClientRect().top < height ){
      active = '#screen_1';
    }
    
    if ( $screen_2[0].getBoundingClientRect().top * 2 < height ){
      active = '#screen_2';
    }
    
    if ( $screen_3[0].getBoundingClientRect().top * 2 < height ){
      active = '#screen_3';
    }
    
    if ( $screen_4[0].getBoundingClientRect().top * 2 < height ){
      active = '#screen_4';
    }
    
    var $svg_1, $svg_2, $svg_3;
    
    if ( $('body').is('.mobile') ){
      
      $svg_1 = $($('.promo-phone-animation')[0])
      $svg_2 = $($('.promo-phone-animation')[1])
      $svg_3 = $($('.promo-phone-animation')[2])
      
      
    } else {
      
       $svg_1 = $svg_2 = $svg_3 = $($('.promo-phone-animation'))
      
    }
    
    
    
    if ( active == '#screen_1' ){
      window.startAnim_1($svg_1);
      window.animation_show(1, $svg_1);

    } else {
      window.animation_hide(1, $svg_1);

    }
    
    if ( active == '#screen_2' ){
      window.startAnim_2($svg_2);
      window.animation_show(2, $svg_2);

    } else {
      window.animation_hide(2, $svg_2);

    }
    
    if ( active == '#screen_3' || active == '#screen_4' ){
      window.startAnim_3($svg_3);
      window.animation_show(3, $svg_3);
      

    } else {
      window.animation_hide(3, $svg_3);
      
    }
      
    console.log(active);
    
    $('.s-control__item').removeClass('s-control__item_active');
    $(`.s-control__item[data-screen="${active}"]`).addClass('s-control__item_active');
    
    
  }
  
  
});
$(function(){
  
  var slides = $('#slider').find('.slider__s').length;
  var active = 0;
  
  var $prev = $('#slider').find('.slider__c-prev');
  var $next = $('#slider').find('.slider__c-next');
  var $container = $('#slider').find('.slider__inner');
  
  slide();
  
  $('#slider').find('.slider__c').click(function(){
    slide( $(this) );
  });
  
  function slide(control){
    
    if (control){
       
      if (control.is('.slider__c-prev') && active > 0){
        active--;
      }

      if (control.is('.slider__c-next') && active < slides-1){
        active++;
      }
    }
    
    $prev.removeClass('disabled');
    $next.removeClass('disabled');
    
    if ( active == 0 ){
      $prev.addClass('disabled');
    } 
    
    if ( active == slides-1 ){
      $next.addClass('disabled');
    } 
    
    $container.css({
      'margin-left' : '-' + (active * 100 ) + 'vw'
    });
    
  }
});
$(function(){
  $('.tab div').click(function(){
    var $forEl = $('[data-id='+ $(this).parent().data('for') +']');
    var $qa = $forEl.find('[data-id=' + $(this).data('qa') + ']');
    
    $forEl.find('.qa').removeClass('qa_selected');
    $qa.addClass('qa_selected');
    
    $(this).parent().find('div').removeClass('selected');
    $(this).addClass('selected');
    
    
    var event = {
				event: 'cta_go-to-switch-q&a',
				event_action: 'click',
				event_category: 'cta',
				event_label: $(this).text()
			};
      
		window.dataLayer.push(event); 
    
    
    var offset;
    
    if ( $(this).data('qa') == 1 ){
      offset = 0;
      
    } else if ( $(this).data('qa') == 2 ){
      offset = 0;
      
    } else if ( $(this).data('qa') == 3 ){
      offset = 500;
      
    }
    
    
    
    $(this).parent().animate({
      scrollLeft : offset
    },300)
    
  });
});
